import Modal from "./Modal";

export default {
  components: {
    Modal,
  },
  data() {
    return {
      src: [
        "https://n1arthn1.github.io/img/ali.7e3a582f.webp",
        "https://n1arthn1.github.io/img/bbAli.webp",
        "https://n1arthn1.github.io/img/fa&bb.webp",
      ],
      isModalVisible: false,
      currentSrc: 0,
    };
  },
  methods: {
    changePhoto: function() {
      if (this.currentSrc < this.src.length - 1) {
        this.currentSrc++;
      } else {
        this.currentSrc = 0;
      }
    },
    showModal() {
      this.isModalVisible = true;
    },
    closeModal() {
      this.isModalVisible = false;
    },
    swipeHandler() {
      this.$router.push("professional");
    },
  },
};
